@import (reference) '~@stremio/stremio-colors/less/stremio-colors.less';

.share-prompt-container {

    .ipc-rating-display {
        display: flex;
        align-items: center;
        flex-direction: column;
        z-index: 0;
        position: fixed;
        top: 30%;
        left: 0;
        width: 100%;

        @media screen and (max-width: 640px) {
            visibility: hidden;
        }

        .ipc-rating-display__star {
            height: 5.75rem;
            width: 5.75rem;
            color: var(--primary-accent-color);
        }

        .ipc-rating-display__rating {
            font-family: Roboto, Helvetica, Arial, sans-serif;
            font-size: 1rem;
            font-weight: 400;
            line-height: 1.5rem;
            letter-spacing: .03125em;
            text-transform: none;
            -webkit-font-smoothing: auto;
            font-smooth: auto;
            font-size: 1.375rem;
            margin: 0;
            padding: 0;
            transform: translateY(-3.25rem);
            width: fit-content;
            color: white;
        }
    }

    .buttons-container {
        display: flex;
        flex-direction: row;
        margin-top: 1px;

        .option-input-container {
            padding: 1rem 1.5rem;

            &.button-container {
                justify-content: center;
                height: 3.5rem;
                border-radius: 3.5rem;
                background-color: var(--primary-accent-color);

                &:hover {
                    outline: var(--focus-outline-size) solid var(--primary-foreground-color);
                    background-color: transparent;
                }

                .label {
                    font-weight: 500;
                }
            }
        }

        .button-container {
            flex: 1;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            padding: 1rem;

            &:not(:last-child) {
                margin-right: 1rem;
            }

            .icon {
                flex: none;
                height: 1.2rem;
                margin-right: 1rem;
                fill: @color-surface-light5-90;
            }

            .label {
                flex-grow: 0;
                flex-shrink: 1;
                flex-basis: auto;
                max-height: 2.4em;
                font-size: 1.1rem;
                font-weight: 500;
                text-align: center;
                color: @color-surface-light5-90;
            }
        }

        .facebook-button {
            background-color: var(--color-facebook);

            &:focus {
                outline-color: @color-background-dark5;
            }

            &:hover {
                background-color: #5799ef;
            }
        }

        .uninstall-button-container {
            .label {
                color: #5799ef;
            }
        }

    }

    .option-name-container {
        justify-content: flex-start;
        padding: 1rem 1rem 1rem 0;
        margin-right: 2rem;
    }


    .loading {
        display: inline-block;
        width: 15px;
        height: 15px;
        border: 3px solid rgba(255, 255, 255, .3);
        border-radius: 50%;
        border-top-color: #fff;
        animation: spin 1s ease-in-out infinite;
        -webkit-animation: spin 1s ease-in-out infinite;
    }

    @keyframes spin {
        to {
            -webkit-transform: rotate(360deg);
        }
    }

    @-webkit-keyframes spin {
        to {
            -webkit-transform: rotate(360deg);
        }
    }

}

.user-info-container {
    display: flex;
    padding: 1.2rem 1rem;
    border-radius: var(--border-radius);

    .avatar-container {
        flex: none;
        height: 4rem;
        width: 4rem;
        border-radius: 50%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-origin: content-box;
        background-clip: content-box;
        opacity: 0.9;
    }

    &:hover,
    &:focus {
        background-color: var(--overlay-color);
    }

    .user-info-details {
        flex: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-left: 1rem;

        .email-container {
            flex: none;
            margin-bottom: 0.5rem;

            .email-label {
                flex: 1;
                color: var(--primary-foreground-color);
                display: flex;
                align-items: center;

                .rate {
                    padding-top: 4px;
                    padding-left: 5px;
                }
            }
        }
    }
}

.user-noinfo-details {
    display: flex;
    padding: 2.5rem 5rem;
    border-radius: var(--border-radius);
    color: var(--primary-foreground-color);
}

.score-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-content: space-around;

    .score-container-col {
        display: flex;
        padding: 1.5rem 1rem;
        border-radius: var(--border-radius);
        max-width: 50%;

        .avatar-container {
            flex: none;
            height: 4rem;
            width: 4rem;
            border-radius: 50%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            background-origin: content-box;
            background-clip: content-box;
            opacity: 0.9;
        }

        &:hover,
        &:focus {
            background-color: var(--overlay-color);
        }

        .user-info-details {
            flex: auto;
            display: flex;
            flex-direction: column;
            margin-left: 1rem;

            .email-container {
                flex: none;
                margin-bottom: 0.5rem;

                .email-label {
                    flex: 1;
                    color: var(--primary-foreground-color);
                    display: flex;
                    align-items: center;

                    .rate {
                        font-size: 40px;
                        letter-spacing: .016em;
                        font-family: "Franklin Gothic",-apple-system,BlinkMacSystemFont,"PT Sans",Arial,sans-serif;
                    }
                }
            }
        }

        .critics-info-details {
            opacity: 0.3;
            cursor: not-allowed;
        }
    }

}